*, *:after, *:before {
  box-sizing: border-box;
}

ul {
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}
ul li {
  list-style: none;
}

:focus {
  outline: 0;
}

button,
html [type=button],
[type=reset],
[type=submit],
input[type],
textarea {
  appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -webkit-appearance: none;
}

input[type=password]::-ms-reveal,
input[type=password]::-ms-clear,
.form-control::-ms-reveal,
.form-control::-ms-clear {
  display: none;
}

